import React from 'react';
import styled from 'styled-components';

import check from '../../assets/check.svg';

const Check = styled.span`
  display: block;
  width: 24px;
  height: 24px;
  min-width: 24px;
  margin-right: 16px;
  border-radius: 50%;
  background-color: #fff;
  transition: background-color .1s ease-in-out;
`;

const CheckboxWrapper = styled.label`
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0.21px;
  line-height: 24px;
  margin: ${p => p.margin ? p.margin : '20px 0'};
  
  input {
    display: none;
    
    &:checked ~ ${Check} {
      background: #56C0EF url(${check}) no-repeat 55% 50%;
    }
  }
`;

export const Checkbox = ({id, name, children, value, onChange, ...props}) => (
 <CheckboxWrapper htmlFor={id} {...props}>
   <input checked={value} onChange={onChange} id={id} type="checkbox" name={name || id}/>
   <Check/>
   <span>{children}</span>
 </CheckboxWrapper>
);


