import React from 'react';
import styled from 'styled-components';

import {Wrapper, ScreenTitle, ScreenDescription} from '../../ui';

import {PrimaryButton, Text} from 'ui';

const ImgContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
`;

export const Screen = ({title, subTitle, desc, img, button}) => (
 <Wrapper>
   <ScreenTitle>{title}</ScreenTitle>
   <ScreenDescription>{subTitle}</ScreenDescription>
   <ImgContainer>
     <img src={img} alt=""/>
   </ImgContainer>
   <Text>{desc}</Text>
   <PrimaryButton
    margin='0 0 35px'
    background={button.color}
    onClick={button.onClick}
   >{button.text}</PrimaryButton>
 </Wrapper>
);


