const css = String.raw;

export const font = {
  main: 'Lato, sans-serif'
};

export const globalStyles = css`
  html, body {
    font-size: 16px;
    font-family: ${font.main};
    -webkit-font-smoothing: antialiased;
    margin: 0;
    padding: 0;
  }
  
  a {
    color: inherit;
    text-decoration: none;
    
    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }
  
  * {
    box-sizing: border-box;
  }
  
  h1, h2, h3, h4 {
    margin: 0;
  }
  
  ul, li {
    margin: 0;
    padding: 0;
  }
  
  .modal-content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;  
    width: 100%;
    max-width: 750px;
    margin: 20px 40px;
    padding: 30px 40px;
    background: rgb(255, 255, 255);
    overflow-y: auto;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 1px 40px -11px #2D2342;
    -webkit-overflow-scrolling:touch;
  }
  
  .modal-overlay {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1001;
    background-color: rgba(0, 0, 0, 0.8);
    overflow-y: auto;
	  -webkit-overflow-scrolling:touch;
  }
  
  .watched-content {
    padding: 0;
  }
`;


