import React from 'react';
import styled, {keyframes} from 'styled-components';

const HideKeyframe = keyframes`
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
`;

const Dot = styled.span`
  animation: ${HideKeyframe} ${p => p.count * .3}s infinite;
  animation-delay: ${p => p.index * .3}s;
`;

export const LoaderDots = ({children}) => (
 children.split('').map(
  (dot, index, array) => <Dot key={index} index={index} count={array.length}>{dot}</Dot>
 )
);


