import React, {Component} from 'react';
import styled from 'styled-components';

import {Wrapper, ScreenTitle, ScreenDescription} from '../ui';
import del from '../assets/del.svg';

const PincodeDisplayWrapper = styled.div`
  display: flex;
  margin-top: 100px;
`;

const PincodeNumber = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 58px;
  margin: 0 4px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, .08);
  color: white;
  font-weight: 800;
  font-size: 18px;
  
  &:nth-child(3) {
    margin-right: 10px;
  }
  &:nth-child(4) {
    margin-left: 10px;
  }
`;

const KeyboardWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  margin-top: auto;
  padding: 10px;
  background-color: white;
`;

const KeyboardItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
  flex-basis: 33.3%;
  font-size: 28px;
  color: #504A8E;
  letter-spacing: 0;
  text-align: center;
`;

export class Pincode extends Component {
  state = {
    pin: [],
    length: 6
  };

  onClick = (num) => {
    if (this.state.pin.length < this.state.length) {
      this.setState({
        pin: [...this.state.pin, num]
      });
    }
  };

  onDelete = () => {
    if (this.state.pin.length > 0) {
      this.setState({
        pin: this.state.pin.slice(0, this.state.pin.length - 1)
      });
    }
  };

  render() {
    return (
     <Wrapper>
       <ScreenTitle>Create your PIN code</ScreenTitle>
       <ScreenDescription>Bitcoin is different - it cannot be safely held w</ScreenDescription>
       <PincodeDisplay value={this.state.pin} length={this.state.length} hide={true}/>
       <Keyboard onClick={this.onClick} onDelete={this.onDelete}/>
     </Wrapper>
    );
  }
}

const PincodeDisplay = ({value, length, hide}) => (
 <PincodeDisplayWrapper>
   {[...new Array(+length).keys()].map(index => (
    <PincodeNumber key={index}>{value[index] || ''}</PincodeNumber>
   ))}
 </PincodeDisplayWrapper>
);

const Keyboard = ({onClick, onDelete}) => (
 <KeyboardWrapper>
   <KeyboardItem onClick={() => onClick(1)}>1</KeyboardItem>
   <KeyboardItem onClick={() => onClick(2)}>2</KeyboardItem>
   <KeyboardItem onClick={() => onClick(3)}>3</KeyboardItem>
   <KeyboardItem onClick={() => onClick(4)}>4</KeyboardItem>
   <KeyboardItem onClick={() => onClick(5)}>5</KeyboardItem>
   <KeyboardItem onClick={() => onClick(6)}>6</KeyboardItem>
   <KeyboardItem onClick={() => onClick(7)}>7</KeyboardItem>
   <KeyboardItem onClick={() => onClick(8)}>8</KeyboardItem>
   <KeyboardItem onClick={() => onClick(9)}>9</KeyboardItem>
   <KeyboardItem/>
   <KeyboardItem onClick={() => onClick(1)}>0</KeyboardItem>
   <KeyboardItem onClick={onDelete}>
     <img src={del} alt=""/>
   </KeyboardItem>
 </KeyboardWrapper>
);


