import React, {Component} from 'react';
import styled from 'styled-components';
import Modal from 'react-modal';

import {PrimaryButton, TextButton, HiveExtendLogo, Text, CircleSpinner, LoaderDots} from 'ui';
import {Wrapper} from '../ui';

Modal.setAppElement('#root');

const LogoContainer = styled.div`
  margin-top: 18vh;
`;

const ControlText = Text.extend`
  margin: 26px 0 auto;
`;

const RestoreButton = TextButton.extend`
  color: white;
  margin-top: 26px;
  margin-bottom: 35px;
`;

const ModalText = styled.p`
  margin-left: 10px;
  font-size: 18px;
  color: #414141;
  letter-spacing: 0.24px;
  line-height: 28px;
  font-weight: 800;
`;

export class WelcomeScreen extends Component {
  state = {
    showModal: false
  };

  openModal = () => {
    this.setState({showModal: true});

    setTimeout(() => {
      this.props.history.push(`${this.props.match.url}/bitcoin`);
    }, 2000)
  };

  render() {
    return (
     <Wrapper>
       <LogoContainer>
         <HiveExtendLogo/>
       </LogoContainer>
       <ControlText>Take control of your money, get started with bitcoin.</ControlText>
       <PrimaryButton margin='auto 0 0' onClick={this.openModal}>Create Vault</PrimaryButton>
       <RestoreButton>Restore from backup</RestoreButton>

       <Modal
        isOpen={this.state.showModal}
        className="modal-content"
        overlayClassName="modal-overlay"
       >
         <CircleSpinner theme={{size: 30}}/> <ModalText>Creating Vault<LoaderDots>...</LoaderDots></ModalText>
       </Modal>
     </Wrapper>
    );
  }
}


