import React, {Component} from 'react';

import {Text, PrimaryButton} from 'ui';
import {Wrapper, ScreenTitle, TextArea} from '../ui';

export class Phrase extends Component {
  render() {
    return (
     <Wrapper>
       <ScreenTitle>Please carefully write down this phrase</ScreenTitle>
       <TextArea disabled innerRef={el => this.textarea = el} name="phrase" rows="4" value={this.props.phrase}/>
       <Text margin='auto 0 0'>We will confirm on the next screen.</Text>
       <PrimaryButton onClick={() => {
         this.props.history.push('verify');
       }} margin='16px 0 35px'>I have written it down</PrimaryButton>
     </Wrapper>
    )
  }
};


