import styled from 'styled-components';

export const TextArea = styled.textarea`
  margin-top: 45px;
  width: 100%;
  padding: 16px 25px;
  border: 2px dashed rgba(255, 255, 255, .4);
  border-radius: 4px;
  outline: none;
  background-color: rgba(0, 0, 0, .08);
  color: white;
  text-align: center;
  font-size: 18px;
  letter-spacing: 0.24px;
  line-height: 28px;
`;


