import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import randomWords from 'services/random-words';

import {
  WelcomeScreen,
  NoBitcoin,
  Watched,
  Phrase,
  Verify,
  TermOfUse,
  Pincode
} from './screens';

const Context = React.createContext();

export class Welcome extends Component {
  state = {
    phrase: randomWords({ min: 7, max: 15, join: ' ' })
  };

  newPhrase = () => {
    this.setState({phrase: randomWords({ min: 7, max: 15, join: ' ' })})
  };

  render() {
    const url = this.props.match.url;

    return (
     <Context.Provider
      value={{ phrase: this.state.phrase, newPhrase: this.newPhrase }}
     >
       <Route exact path={`${url}`} component={WelcomeScreen}/>
       <Route path={`${url}/bitcoin`} component={NoBitcoin}/>
       <Route path={`${url}/watched`} component={Watched}/>
       <Route path={`${url}/phrase`} component={withContext(Phrase)}/>
       <Route path={`${url}/verify`} component={withContext(Verify)}/>
       <Route path={`${url}/terms`} component={TermOfUse}/>
       <Route path={`${url}/pincode`} component={Pincode}/>
     </Context.Provider>
    )
  }
}

const withContext = (Component) => {
  return function ConnectedComponent (props) {
    return (
     <Context.Consumer>
       {contextProps => <Component {...props} {...contextProps}/>}
     </Context.Consumer>
    )
  }
};


