import {axiosInstance} from 'services/axios-instance';

export class Api {
  async getCallbackData(sessionId) {
    return await axiosInstance({
      method: 'GET',
      url: `sessions/${sessionId}/callback`,
      headers: {
        'Accept-Language': 'ru'
      }
    });
  }

  async getAllClaims() {
    return axiosInstance({
      method: 'GET',
      url: `claim_types`,
    })
  }

  async saveCallbackData(sessionId, data) {
    return await axiosInstance({
      method: 'POST',
      url: `sessions/${sessionId}/callback`,
      data: data,
    })
  }
}


