import React from 'react';
import styled, {keyframes, ThemeProvider} from 'styled-components';

const Spin = keyframes`
  0% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: .5;
    transform: scale(.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
`;

const Spiner = styled.div`
  position: relative;
  height: ${p => p.theme.size}px;
  width: ${p => p.theme.size}px;
`;

const Circle = styled.div`
  position: absolute;
  width: ${p => p.theme.size / 5}px;
  height: ${p => p.theme.size / 5}px;
  background-color: #7956EF;
  border-radius: 50%;
  //opacity: ${p => 1.1 - p.index * .1};
  // transform: scale(${p => 1.1 - p.index * .1});
  animation: ${Spin} 1.5s infinite ease-in-out;
  
  &:nth-child(1) {
    top: ${p => p.theme.size / 10}px;
    right: ${p => p.theme.size / 10}px;
    animation-delay: 0s;
  }
  
  &:nth-child(2) {
    top: calc(50% - ${p => p.theme.size / 4.71 / 2}px);
    right: 0;
    animation-delay: .1s;
  }
  
  &:nth-child(3) {
    bottom: ${p => p.theme.size / 10}px;
    right: ${p => p.theme.size / 10}px;
    animation-delay: .2s;
  }
  
  &:nth-child(4) {
    bottom: 0;
    right: calc(50% - ${p => p.theme.size / 4.71 / 2}px);
    animation-delay: .3s;
  }
  
  &:nth-child(5) {
    bottom: ${p => p.theme.size / 10}px;
    left: ${p => p.theme.size / 10}px;
    animation-delay: .4s;
  }
  
  &:nth-child(6) {
    top: calc(50% - ${p => p.theme.size / 4.71 / 2}px);
    left: 0;
    animation-delay: .5s;
  }
  
  &:nth-child(7) {
    top: ${p => p.theme.size / 10}px;
    left: ${p => p.theme.size / 10}px;
    animation-delay: .6s;
  }
  
  &:nth-child(8) {
    top: 0;
    right: calc(50% - ${p => p.theme.size / 4.71 / 2}px);
    animation-delay: .7s;
  }
`;

const CircleSpinner = (props) => (
 <ThemeProvider theme={props.theme}>
   <Spiner>
     <Circle index={1}/>
     <Circle index={2}/>
     <Circle index={3}/>
     <Circle index={4}/>
     <Circle index={5}/>
     <Circle index={6}/>
     <Circle index={7}/>
     <Circle index={8}/>
   </Spiner>
 </ThemeProvider>
);

CircleSpinner.defaultProps = {
  theme: {
    size: 40
  }
};

export {CircleSpinner};


