import React from 'react';

import mark from '../assets/mark.svg';
import {Screen} from '../ui/templates';

export const NoBitcoin = ({history}) => (
 <Screen
  title='No backup, no bitcoin'
  subTitle='Since only you control your money, youll need to save your babackup phrase in case this app is deleted.'
  img={mark}
  desc='Your vault is never saved to cloud storage or standard device backups.'
  button={{
    text: 'Continue',
    color: '#EDCC1D',
    onClick: () => history.push(`watched`)
  }}
 />
);


