import React from 'react';
import styled from 'styled-components';

import {HiveLogo} from 'ui';

const HeaderWrapper = styled.header`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 24px;
`;

const LogoContainer = styled.div`
  height: 40px;
  width: 40px;
  color: #6743DF;
`;

export const Header = () => (
    <HeaderWrapper>
      <LogoContainer>
        <HiveLogo/>
      </LogoContainer>
    </HeaderWrapper>
);


