import React, {Component} from 'react';
import styled from 'styled-components';

import {PrimaryButton, Text, TextButton} from 'ui';
import {Wrapper, ScreenTitle, TextArea, Modal} from '../ui';
import successSvg from '../assets/success.svg';
import errorSvg from '../assets/error.svg';

const WordButton = PrimaryButton.extend`
  width: auto;
  padding: 8px 12px;
  border-radius: 6px;
  margin: 10px 8px;
  height: 44px;
  
  ${p => p.disabled && `opacity: .5;`}
`;

const Buttons = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: auto;
  margin-bottom: 32px;
`;

const ModalButtons = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 24px;
`;

export class Verify extends Component {
  state = {
    words: this.props.phrase.split(' ').map(word => ({text: word, active: false})),
    phrase: '',
    showModal: false,
    success: false
  };

  activeWordsLenght = () => this.state.words.reduce((acc, item) => (!item.disabled ? acc + 1 : acc), 0);

  onClick = ({target}) => {
    const index = +target.dataset.index;
    const text = this.state.words[index].text;

    this.setState({
      words: this.state.words.map((item, itemIndex) => itemIndex === index ? {...item, disabled: true} : item),
      phrase: this.state.phrase + ' ' + text
    });
  };

  clear = () => {
    this.setState({
      words: this.state.words.map(item => ({...item, disabled: false})),
      phrase: ''
    });
  };

  confirm = () => {
    this.setState({
      showModal: true,
      success: this.state.phrase.trim() === this.props.phrase.trim()
    });
  };

  reset = () => {
    this.props.newPhrase();
    this.props.history.goBack();
  };

  render() {
    return [
      <Wrapper key='wrapper'>
        <ScreenTitle>Lets verify your backup phrase</ScreenTitle>
        <TextArea value={this.state.phrase} rows='4'/>
        {
          this.activeWordsLenght() === 0 ?
           <Confirm onClear={this.clear} onConfirm={this.confirm}/> :
           <ButtonsList words={this.state.words} onClick={this.onClick}/>
        }
      </Wrapper>,
      this.state.success ?
       <SuccessModal showModal={this.state.showModal} onClick={() => this.props.history.push('terms')} key='success'/> :
       <ErrorModal reset={this.reset} again={this.clear} showModal={this.state.showModal} key='error'/>
    ]
  }
}

const ButtonsList = ({words, onClick}) => (
 <Buttons>
   {words.map(
    (word, index) => (
     <WordButton
      disabled={word.disabled}
      data-index={index}
      key={index}
      onClick={onClick}
     >{word.text}</WordButton>)
   )}
 </Buttons>
);

const Confirm = ({onClear, onConfirm}) => [
  <Text key='text' margin='auto 0 16px'>Is this correct?</Text>,
  <PrimaryButton onClick={onConfirm} key='confirm'>Confirm</PrimaryButton>,
  <TextButton onClick={onClear} key='clear' color='#fff' margin='30px 0'>Clear</TextButton>
];

const SuccessModal = ({showModal, onClick}) => (
 <Modal
  key='modal'
  className='watched-content'
  img={successSvg}
  headerColor='#6FC221'
  isOpen={showModal}
  title='Your bitcoin vault is backed up!'
  desc='Be sure to store your recovery phrase in a secure place. If this app is deleted, your money cannot be recovered without it.'
 >
   <TextButton
    color='#6FC221'
    margin='0 0 24px'
    onClick={onClick}
   >Got it</TextButton>
 </Modal>
);

const ErrorModal = ({showModal, reset, again}) => (
 <Modal
  key='modal'
  className='watched-content'
  img={errorSvg}
  headerColor='#F45663'
  isOpen={showModal}
  title='Your bitcoin vault is backed up!'
  desc='Be sure to store your recovery phrase in a secure place. If this app is deleted, your money cannot be recovered without it.'
 >
   <ModalButtons>
     <TextButton
      color='#767FAB'
      margin='0 0 24px'
      onClick={reset}
     >Generate new phrase</TextButton>
     <TextButton
      color='#56C0EF'
      margin='0 0 24px'
      onClick={() => this.setState({showModal: false})}
     >Try again</TextButton>
   </ModalButtons>
 </Modal>
);


