import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import { createLogger } from 'redux-logger';
import thunk from 'redux-thunk';
import { createSymbiote } from 'redux-symbiote';

import {Api} from 'services/api';

export const LOADING = {
  failed: -1,
  initial: 0,
  loading: 1,
  ready: 2,
};

export const { actions: partnerActions, reducer: partnerReducer } = createSymbiote({
  status: LOADING.initial,
  errors: null,
  data: null,
  sessionId: null
}, {
  loading: {
    start: (state) => ({ ...state, status: LOADING.loading }),
    failed: (state, error) => ({ ...state, error, status: LOADING.failed }),
    finish: (state, res) => ({ ...state, data: res.data, status: LOADING.ready }),
  },
  setSessionId: (state, sessionId) => ({...state, sessionId: sessionId})
}, 'partner');

export const {actions: claimsActions, reducer: claimsReducer} = createSymbiote({
  status: LOADING.initial,
  errors: null,
  data: null,
}, {
  loading: {
    start: (state) => ({ ...state, status: LOADING.loading }),
    failed: (state, error) => ({ ...state, error, status: LOADING.failed }),
    finish: (state, res) => ({ ...state, data: res.data, status: LOADING.ready }),
  },
}, 'claims');

const reducer = combineReducers({
  partner: partnerReducer,
  claims: claimsReducer
});

export const loadAllClaims = () => (
 async (dispatch, getState, { api }) => {
   dispatch(claimsActions.loading.start());

   try {
     const res = await api.getAllClaims();

     dispatch(claimsActions.loading.finish(res))
   } catch (e) {
     dispatch(claimsActions.loading.failed(e.message))
   }
 }
);

export const loadPartner = (sessionId) => (
 async (dispatch, getState, { api }) => {
    dispatch(partnerActions.loading.start());

    try {
      const partner = await api.getCallbackData(sessionId);

      dispatch(partnerActions.loading.finish(partner))
    } catch (e) {
      dispatch(partnerActions.loading.failed(e.message))
    }
 }
);

export const sendPartnerData = (sessionId, data) => (
 async (dispatch, getState, { api }) => {
   console.log(data);
   dispatch(partnerActions.loading.start());

   try {
     const partner = await api.saveCallbackData(sessionId, data);

     dispatch(partnerActions.loading.finish(partner))
   } catch (e) {
     dispatch(partnerActions.loading.failed(e.message))
   }
 }
);

export function configureStore(rootInitialState = {}) {
  const api = new Api();

  const middleware = [
    createLogger({ collapsed: true }),
    thunk.withExtraArgument({ api }),
  ];

  const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

  return createStore(
   reducer,
   rootInitialState,
   composeEnhancers(applyMiddleware(...middleware)),
  )
}


