import React from 'react';
import styled from 'styled-components';
import ReactModal from 'react-modal';

const ModalBody = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
`;

const ModalHeader = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 16px 0;
  background-color: ${p => p.bg};
`;

const ModalTitle = styled.h3`
  padding: 0 24px;
  margin-top: 24px;
  font-size: 20px;
  color: #504A8E;
  letter-spacing: 0.27px;
  line-height: 30px;
  text-align: center;
`;

const ModalDesc = styled.p`
  padding: 0 24px;
  font-size: 16px;
  color: #767FAB;
  letter-spacing: 0.21px;
  text-align: center;
  line-height: 24px;
`;

export const Modal = ({img, headerColor, title, desc, children, isOpen, className}) => (
 <ReactModal
  key='modal'
  isOpen={isOpen}
  className={"modal-content " + className}
  overlayClassName="modal-overlay"
 >
   <ModalBody>
     <ModalHeader bg={headerColor}>
       <img src={img} alt=""/>
     </ModalHeader>
     <ModalTitle>{title}</ModalTitle>
     <ModalDesc>{desc}</ModalDesc>
     {children}
   </ModalBody>
 </ReactModal>
);


