import styled from 'styled-components';

const css = String.raw;

export const Button = styled.button`
  background: none;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  
  background: ${p => p.background};
  margin: ${p => p.margin};
  color: ${p => p.color};
  
  ${p => p.disabled && css`opacity: .5`}
`;

const PrimaryButton = Button.extend`
  padding: 12px 0;
  width: 100%;
  border-radius: 22px;
  color: white;
  text-transform: uppercase;
  font-weight: 900;
  line-height: 24px;
  text-align: center;
  min-height: 46px;
`;

PrimaryButton.defaultProps = {
  background: '#56C0EF'
};

export {PrimaryButton};

export const TextButton = Button.extend`
  font-size: 16px;
  letter-spacing: 0.44px;
  text-align: center;
  line-height: 24px;
  font-weight: bold;
`;

export const ScanButton = Button.extend`
  padding: 10px 25px;
  border-radius: 3px;
  color: white;
  background: linear-gradient(45deg, #925DFB 0%, #6743DF 100%);
  
  ${p => p.fluid && 'width: 100%'}
  ${p => p.margin && `margin: ${p.margin}`}
`;


