import axios from 'axios';

const headers = process.env.NODE_ENV === 'development' ? {} : {};

export const axiosInstance = axios.create({
  baseURL: process.env.REACT_APP_API_HOST,
  headers: {
    ...headers,
    'x-profile-id': 'bb238be3-a3e1-4274-98df-0b43d200b668'
  }
});


